$scrollLength: 100vh;
@media screen and (prefers-reduced-motion: no-preference) {
  html,
  body {
    scroll-behavior: smooth;
  }
}
.king-container {
  .homepage {
    height: 100vh;
    animation: expand 0.8s ease forwards;
    background-color: #fff;
    position: relative;
    transition: all 0.8s ease;
    overflow: auto;

    .back-to-top-wrapper {
      // uncomment to visualize "track"
      // outline: 1px solid red;
      position: absolute;
      top: $scrollLength;
      right: 0.25rem;
      bottom: -5em;
      width: 3em;
      pointer-events: none;
    }

    .back-to-top-link {
      // `fixed` is fallback
      position: fixed;
      // preferred positioning, requires prefixing for most support, and not supported on Safari
      // @link https://caniuse.com/#search=position%3A%20sticky
      position: sticky;
      // reinstate clicks
      pointer-events: all;
      // achieves desired positioning within the viewport
      // relative to the top of the viewport once `sticky` takes over, or always if `fixed` fallback is used
      top: calc(100vh - 5rem);
    
      // basic styling
      display: inline-block;
      text-decoration: none;
      font-size: 2rem;
      line-height: 3rem;
      text-align: center;
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      padding: 0.25rem;
    
      // "pretty" styles, including states
      transition: transform 80ms ease-in;
    
      &:hover,
      &:focus {
        transform: scale(1.1);
      }
    
      &:focus {
        outline: none;
      }
    }

    .body {
      //align-items: center;
      display: flex;
      flex-direction: column;
      padding: 40px 50px;

      .gabs {
        text-decoration: none;
        pointer-events: none;
        align-self: center;
        color: #86d780;
        font-weight: 400;
        font-size: 65px;
        font-family: "MuseoModerno";
      }
      .title-surname {
        color: #3a5a6b;
        font-weight: 400;
        font-size: 65px;
        font-family: "MuseoModerno", cursive;
        margin: 0;
        text-align: center;
      }
      .bio-homepage {
        float: right;
        padding: 20px 50px;
        font-size: 30px;
       // font-family: 'Nunito';
        .text {
          color: #6c6c6c;
          font-weight: 200;
          text-align: center;
        }
        .name {
          color: #86d780;
          font-weight: 800;
          text-decoration: none;
          pointer-events: none;
        }
        .emphasis {
          color: #23aa8f;
          font-weight: 600;
          text-decoration: none;
          pointer-events: none;
        }
      }
    }
    .buttons-homepage {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      .button-a {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        font-size: 24px;
        color: #007882;
        width: 250px;
        height: 50px;
        -webkit-border-radius: 30px;
        -moz-border-radius: 30px;
        border-radius: 30px;
        border: 1px solid #007882;
        background-color: transparent;
        cursor: pointer;
        margin: 20px;
        &:focus {
          outline: transparent;
        }
        &:hover {
          cursor: pointer;
          border: 1px solid #86d780;
          color: #86d780;
        }
        .icon {
          margin: 0;
          padding: 0;
          display: block;
          align-self: center;
          width: 20px;
          height: 20px;
          background: url(../../assets/linkedin.svg);
          background-size: 20px 20px;
        }
      }
    }
  }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    z-index: -10;
    background-color: #86d780;
    //background-color: #007882;
  }
  @keyframes expand {
    0% {
      transform: translateX(2100px);
    }
    100% {
      transform: translateX(0px);
    }
  }
}
@keyframes slideIn {
  0% {
    transform: translateX(500px) scale(0.2);
  }
  100% {
    transform: translateX(0px) scale(1);
  }
}
@keyframes slideUp {
  0% {
    transform: translateY(300px);
  }
  100% {
    transform: translateY(0px);
  }
}

@media screen and (min-device-width: 1200px) {
  .king-container {
    .homepage {
      .buttons-homepage {
        .button-a {
          height: 60px;
          width: -moz-fit-content;
          width: 250px;
        }
      }
    }
  }
}

